/*
IDs
*/
#aboutUs{
    background-color:#006E91;
    color:white;
    margin-top:60px;
    padding:10px;
    text-align: center;

}
#aboutUsContent{
    width:85%;
    float: left;
    margin-top:40px;
    margin-bottom:40px;
    
}
#aboutPic{
    border:1px solid black;
    border-radius: 5%;
    height:300px;
    width: auto;
}
#aboutUsGrid{
margin-left:5%;    
}
#backgroundImg{
    height: 100px;
    width: 200px;
    float:left;
}
#container{
    margin-top: 5%;
}
#event-title{
    text-align: center;
}
#table-event img{
    margin:20px;
    border:1px solid black;
    border-radius: 10%;
}
#homeBlock2{
    width:50%;
    text-align: center;
    float:right;
}
#homeEvent strong{
    font-size:2em;
    padding:5%;
    margin-bottom:20px;
}
/*
Class
*/
.ad{
    padding:10px;
}
.adSide{
    background-color: grey;
    width:15%;
    float:right;
}
.adBar{
    background-color: grey;
    height:75px;
    width: 75%;
    margin:auto;
}
.homeEvent{
    height:300px;
    width:auto;
}
/*
Tags
*/
button{
    margin:10px;
}




